import { forwardRef, FunctionComponent, PropsWithoutRef, Ref } from 'react';
import styled, { css } from 'styled-components';
import { readableColor } from 'polished';

import {
  Button,
  StyledButton,
  Text,
  Icon,
  registerIcon,
  useI18n,
  tryCatch,
  defaultThemeProp,
  BaseProps,
  ForwardProps,
  useDirection,
  NoChildrenProp
} from '@pega/cosmos-react-core';
import * as timesIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/times.icon';

registerIcon(timesIcon);

export interface AppAnnouncementProps extends BaseProps, NoChildrenProp {
  /** Heading text for the announcement. */
  heading?: string;
  /** Description text for the announcement. */
  description?: string;
  /** Announcement details to be displayed in form of bulleted list. */
  details?: string[];
  /** Url to open with What's new link. */
  whatsNewLink?: string;
  /** Content to populate the announcement button. */
  whatsNewText?: string;
  /** An image source for the decorative background. */
  image?: string;
  /** Callback when user dismisses the announcements. */
  onDismiss?: () => void;
  ref?: Ref<HTMLElement>;
}

const StyledDismissButton = styled.button`
  font-size: 1.25rem;
  border: none;
`;

export const StyledAppAnnouncement = styled.article<Pick<AppAnnouncementProps, 'image'>>(props => {
  const {
    theme: {
      base: {
        palette: { 'brand-primary': brandBackground },
        shadow,
        spacing
      },
      components: {
        card: { 'border-radius': borderRadius }
      }
    },
    image
  } = props;

  const { end } = useDirection();
  const color = tryCatch(() => readableColor(brandBackground));

  return css`
    padding: calc(2 * ${spacing});
    background: ${brandBackground};
    border-radius: ${borderRadius};
    color: ${color};
    position: relative;

    &::after {
      background: url('${image}') no-repeat;
      background-position: ${end} bottom;
      background-size: contain;
      background-origin: content-box;
      opacity: 0.2;
      content: '';
      display: block;
      position: absolute;
      width: 50%;
      height: 100%;
      inset-inline-end: 0;
      bottom: 0;
      padding: calc(2 * ${spacing});
    }

    h2 {
      position: relative;
      z-index: 2;
      margin-bottom: ${spacing};
    }

    ${StyledDismissButton} {
      position: absolute;
      top: 0.75rem;
      inset-inline-end: 0.75rem;
      color: inherit;
      z-index: 3;
      &:enabled:hover {
        background: #ffffff19;
      }
      &:enabled:focus {
        box-shadow: ${shadow.focus};
      }
    }

    > div {
      width: 75%;
      position: relative;
      z-index: 2;

      p,
      ul {
        padding-top: calc(2 * ${spacing});
      }

      ul {
        padding-inline-start: calc(2 * ${spacing});
      }

      ${StyledButton} {
        margin-top: calc(2 * ${spacing});
        &:enabled:focus,
        &:not([disabled]):focus {
          box-shadow: ${shadow.focus};
        }
      }
    }
  `;
});

StyledAppAnnouncement.defaultProps = defaultThemeProp;

const AppAnnouncement: FunctionComponent<AppAnnouncementProps & ForwardProps> = forwardRef(
  function AppAnnouncement(
    props: PropsWithoutRef<AppAnnouncementProps>,
    ref: AppAnnouncementProps['ref']
  ) {
    const t = useI18n();
    const {
      heading,
      description,
      details,
      whatsNewLink,
      whatsNewText = t('app_announcement_whats_new_button_label'),
      image,
      onDismiss,
      ...restProps
    } = props;

    return (
      <StyledAppAnnouncement {...restProps} ref={ref} image={image}>
        {onDismiss && (
          <Button
            as={StyledDismissButton}
            variant='simple'
            icon
            onClick={onDismiss}
            aria-label={t('app_announcement_dismiss_button_label_a11y')}
          >
            <Icon name='times' />
          </Button>
        )}
        <Text variant='h2'>{heading || t('app_announcement_details_list_header')}</Text>
        <div>
          {description && <p>{description}</p>}
          {details && (
            <ul>
              {details.map((item, index) => {
                // eslint-disable-next-line react/no-array-index-key
                return <li key={index}>{item}</li>;
              })}
            </ul>
          )}
          {whatsNewLink && (
            <Button variant='secondary' href={whatsNewLink} target='_blank'>
              {whatsNewText}
            </Button>
          )}
        </div>
      </StyledAppAnnouncement>
    );
  }
);

export default AppAnnouncement;
